.card {
    max-width: 400px;
    height: 100%;
    border-radius: 16px;
    background: #FFF;
    cursor: pointer;
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 1120px) {
    .card {
        max-height: 490px;
    }
}

@media screen and (max-width: 900px) {
    .card {
        min-width: 224px;
        max-height: 420px;
    }
}

@media screen and (max-width: 490px) {
    .card {
        width: 288px;
        max-height: 446px;
    }
}